.single-match-card {
    color: black;
    margin-top: 1%;
    margin-bottom: 1%;
    border-radius: 10px;
    background-color: transparent;
    font-size: 15px;
    /* opacity: 0.7; */
    margin-left: auto;
    margin-right: auto;
    width: 97%;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
}

.single-match-card:hover {
    width: 100%;
}